.HomePage{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
}

.hero-text{
    color: black;
    text-align: center;
    font-size: 3.6rem;
    span{
        color: #F9B208; 
    }
}

.h-sub-text{
    text-align: center;
}

.icons{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 0;
    .icon-holder{
        margin-right: 1rem;
        .icon{
           font-size: 2.8rem;
           color: #a4acc4;
           transition: all .4s ease-in-out;
        }
        .fb:hover{
            color: blue;
        }
        .gh:hover{
            color: white;
        }
        .ig:hover{
            color: pink;
        }
        .sp:hover{
            color: yellow;
        }
        .tw:hover{
            color: blue;
        }
        .tt:hover{
            color: white;
        }
    }
}